/*--ORIGINAL LESS---------------------------------------------------------------------------------------------------------------------------------------------*/
@import (less) 'color.less';

.btn {
    border-radius: 2px !important;
}

.button {
    font-weight: bold;
    font-size: 0.750em;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    text-transform: uppercase;
    height: 4.167em;
    min-width: 9.167em;
    box-shadow: none !important;
    // &:focus {
    //     box-shadow: 0 0 0.05em 0.2em rgba(37,137,0,0.6) !important;
    // }
    &.check.tabFocusOn {
        box-shadow: 0 0 0.05em 0.2em rgba(37,137,0,0.6) !important;
    }
}

.tabFocusOn {
    box-shadow: 0 0 0.05em 0.2em rgba(37,137,0,0.6) !important;
}

.button.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.primary-l {
    color: @white;
    background-color: @green;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
    float: left;
}

.button.primary-r {
    color: @white;
    background-color: @green;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
    float: right;
    margin-right: 5px;
}

.button.default {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.default-l {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
    float: left;
    margin-left: 5px;
}

.button.default-r {
    color: @green;
    background-color: @dark_white;
    border-color: @green;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
    float: right;
}

.button.background-green {
  background-color: @white;
  border-color: @green;
  color: @green;
  &:hover {
      background-color: @darkest_gray;
      color: @white;
      border-color: @darkest_gray;
  }
}

.button.background-green[disabled]{
    opacity: 0.5;
}

.button.background-gray {
  background-color: @white;
  border-color: @green;
  color: @green;
  &:hover {
      background-color: @darkest_gray;
      color: @white;
      border-color: @darkest_gray;
  }
}

.button.background-gray[disabled]{
    opacity: 0.5;
}

.button.background-gray-primary {
    background-color: @green;
    border-color: @green;
    color: @white;
    &:hover {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.background-gray-primary[disabled]{
    opacity: 0.5;
}

.button.secondary{
  color: @gray;
    background-color: @white;
    border-color: @gray;
    &:hover:not(.select) {
        background-color: @darkest_gray;
        color: @white;
        border-color: @darkest_gray;
    }
}

.button.outline{
    color: @lightest_gray;
    background-color: Transparent;
    background-repeat:no-repeat;
    border-color: @lightest_gray;
    &:hover:not(.select) {
        background-color: @green;
        color: @lightest_gray;
        border-color: @green;
    }
}

.btn.primary[disabled]{
    opacity: 0.5;
    &:hover {
        color: @white;
        background-color: @green !important;
        border-color: @green !important;
        cursor: not-allowed;
    }
}

.btn.default[disabled]{
    opacity: 0.5;
    // background-color:#FFFFFF !important;
    // color: @light_gray;
    &:hover {
        color: @light_gray;
        background-color: #FFFFFF !important;
        border-color: @lightest_gray !important;
        cursor: not-allowed;
    }
}

.btn.secondary[disabled]{
    &:hover {
        color: @gray;
        background-color: @white !important;
        border-color: @lightest_gray !important;
        cursor: not-allowed;
    }
}

.btn.outline[disabled]{
        color: @light_gray;
        background-color: Transparent !important;
        border-color: @light_gray !important;
        cursor: not-allowed;
    &:hover {
        color: @light_gray;
        background-color: Transparent !important;
        border-color: @light_gray !important;
        cursor: not-allowed;
    }
}

.button-icon {
  font-size: 2em !important;
  display: table-cell !important;
//   top: -0.2125em;
  position: relative;
}

.button-label-icon {
  padding-left: 0.625em;
  display: table-cell !important;
  vertical-align: middle;
}

.button.selection-button {
    width: 29.6875em;
    height: 5.625em;
    background-color: @lightest_gray;
    border: none;
    border-radius: 1.5625em !important;
    -moz-box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    font-size: 1em;
    color: @green;
    text-align: left;
    &:hover {
        background-color: @green;
        color: @white;
    }
    .button-label-icon {
        padding-bottom: 0em;
        padding-left: 4.8em;
        text-transform: none;
        font-weight: normal;
    }
    .button-icon {
        top: 0.12em;
        left: 1em;
    }
}

.button.action {
    color: @darkest_gray;
    background-color: @lightest_gray;
    border-color: @lightest_gray;
    .button-icon {
        color: @green;
    }
    &:hover {
        background-color: @darkest_gray;
        color: @white;
        border: none;
        & .button-icon {
            color: @white;
        }
    }
}

.btn.action[disabled] {
    color: @gray;
    background-color: @dark_white !important;
    border-color: @lightest_gray !important;
    opacity: 0.5;
    &:hover {
        color: @gray;
        background-color: @dark_white !important;
        border: 0.063em solid @lightest_gray !important;
        cursor: default;
        .button-icon {
            color: @green;
        }
    }
    &.green {
        .button-icon {
            color: @green !important;
        }
    }
}


.button.select {
    color: @green;
    border-color: @green;
    height: 4.167em;
    min-width: 7em;
    border-radius: 2.5625em !important;
    &.on {
        background-color: @green !important;
        color: @white !important;
    }
    & .button-icon {
        left: 0.7em;
        top: 0em;
    }
    & .icon-with-text {
        left: 1em;
    }
    .button-label-icon {
        padding-bottom: 0.1125em;
        padding-left:  3.225em;
        padding-right: 3.225em;
    }
      .button-label{
        padding-bottom: 0.1125em;
        padding-left:  3.225em;
        padding-right: 3.225em;
      }
     &[disabled]{
        opacity: 0.5;
        // background-color:#FFFFFF !important;
        // color: @light_gray;
        &:hover{
            opacity:1 !important;
            background-color:#FFFFFF !important;
            color: @light_gray;
        }
        & .button-icon{
          color: @lightest_gray;
        }
     }

    &.lg {
        color:@green;
        height: 5.625em !important;
        min-width: 14.375em !important;
        border-radius: 1.5625em !important;
        font-weight: normal;
        font-size: 1em;
        text-transform: uppercase;
        box-shadow: none !important;
        &.outline{
               color: @lightest_gray ;
                background-color: Transparent ;
                background-repeat:no-repeat ;
                border-color: @lightest_gray;
                &:hover{
                          color: @lightest_gray ;
                          background-color: Transparent ;
                          background-repeat:no-repeat ;
                          border-color: @lightest_gray ;
                }
            & .button-icon {
                color: @lightest_gray;
            }
          }
        &.on {
            background-color: @green !important;
            color: @white !important;
            & .button-icon {
                color: @white !important;
            }
        }
        & .button-icon {
            left: 0.0em;
            top: 0.0em;
            font-size: 1.875em !important;
            color: @green;
        }
        .button-label-icon {
            padding-left: 2.225em;
            white-space: normal;
            width: 0.0625em;
            padding-bottom: 0;
            text-transform: capitalize;
        }
        .allineamento-select {
            text-align: justify;
            vertical-align: middle !important;
            display: inline-block !important;
        }
        .button-icon-check {
            position: absolute;
            margin-left: -1.375em;
            margin-top: -0.188em;
            width: 1em;
            height: 1.125em;
            font-size: 1.35em !important;
        }
        &[disabled]{
              opacity:1 !important;
              background-color:#FFFFFF !important;
              color: @light_gray;
              & .button-icon{
                color: @lightest_gray;
              }
        }
      }
  }

  .button.check {
      height: 4.167em;
      color: @green;
      // min-width: 25em;
      border: 0.063em solid @green;
      background-color: transparent;
      border-radius: 2.5625em !important;
      font-weight: bold;
      font-size: 0.750em;
      text-transform: uppercase;
      box-shadow: none !important;
      min-width:9.167em;
      &.on {
          background-color: @green;
          color: @white;
      }
      .button-label {
          padding-bottom: 0.1125em;
          padding-left:  3.225em;
          padding-right: 3.225em;
      }
      .button-icon {
          color: @green;
          margin-right: 0.313em;
          // left: 1.38em;
          top: -0.1em;
      }
      .allineamento-select {
          margin-left: 1.250em;
          margin-right: 1.250em;
      }
      .button-icon-check {
          position: absolute;
          // margin-left: -1.375em;
          margin-top: -0.188em;
          width: 1em;
          height: 1.125em;
          font-size: 1.35em !important;
          color: @white;
      }

  }

  .button.profile {
      border-radius: 1.563em !important;
      height: 8.750em;
      width: 7.500em;
      min-width: 7.5000em;
      color: @green;
      background-color: @white;
      border: 0.063em solid @green;
      font-weight: normal !important;
      font-size: 1em !important;
      text-transform: capitalize !important;
      &.on {
          background-color: @green;
          color: @white;
          .checkbox-label {
              color: @white !important;
          }
      }
      .icheckbox-container {
          display: block;
          top: -3.0625em;
          position: relative;
      }
      .checkbox-label {
          color: @green;
          font-weight: normal !important;
          font-size: 1em !important;
          text-transform: initial;
          display: block;
          white-space: normal;
          word-wrap: break-word;
          width: 4.375em;
          text-align: left;
          margin-top: 0.625em !important;
      }
      .icheckbox_intesa {
          display: block;
      }
      .button-icon-profile {
          top: 0em;
          // left: 1.7em;
        //   font-size: 3.125em !important;
          width: 3.125em !important;
          height: 3.125em !important;
        //   margin-left: 0.250em;
          &:before {
              font-size: 2.625em;
          }
      }
      
      .button-icon-check {
          top: -0.7em;
          // left: 1.7em;
          font-size: 1.35em !important;
          display: inline;
          position: relative;
          width: 1.500em !important;
          height: 1.313em !important;
          margin-left: 0.3em;
      }
      .button-label-icon {
          top: 0em;
          font-size: 0.75em;
          font-weight: bold;
          font-family: Arial;
          white-space: normal;
          word-wrap: break-word;
          line-height: 1.2em;
          padding-left: 0!important;
      }
      .allineamento-select {
          margin-top: -1em;
          padding-top: 0.625em;
      }
  }

.button.select.outline{
            
      color: @lightest_gray;
      background-color: Transparent;
      background-repeat:no-repeat;
      border-color: @lightest_gray;
      &:hover{
                color: @lightest_gray;
                background-color: Transparent;
                background-repeat:no-repeat;
                border-color: @lightest_gray;
      }
      &[disabled]{
          color: @gray !important;
          background-color: Transparent !important;
          background-repeat:no-repeat;
          border-color: @gray !important;
          &:hover{
              color: @gray;
              background-color: Transparent !important; 
              background-repeat:no-repeat;
              border-color: @gray;
          }
            & .button-icon {
            color: @gray !important;
          }
      }

      & .button-icon {
        color: @lightest_gray;
      }
}
  





.button.select-profile {
    height: 8.75em;
    width: 14.375em;
    color: @green;
    background-color: @white;
    border: 0.063em solid @light_gray;
    font-weight: normal !important;
    font-size: 1em !important;
    text-transform: capitalize !important;
    &.on {
        background-color: @green;
        color: @white;
        .checkbox-label {
            color: @white !important;
        }
    }
    .icheckbox-container {
        display: block;
        top: -3.0625em;
        position: relative;
    }
    .checkbox-label {
        color: @green;
        font-weight: normal !important;
        font-size: 1em !important;
        text-transform: initial;
        display: block;
        white-space: normal;
        word-wrap: break-word;
        width: 4.375em;
        text-align: left;
        margin-top: 0.625em !important;
    }
    .icheckbox_intesa {
        display: block;
    }
    .button-icon {
        top: 0em;
        left: 1.7em;
        font-size: 4.375em!important;
    }
}

.button.select-icon-text {
    height: 3em;
    min-width: 18.75em;
    border-radius: 1.5em !important;
    color: @darkest_gray;
    background-color: @lightest_gray;
    font-weight: bold;
    font-size: 1em;
    text-transform: uppercase;
    &.on {
        background-color: @lightest_green;
        color: @green;
        .button-icon {    
            background-color: @green;
            color: @white;
        }
    }
    .button-icon {
        top: -0.5125em;
        left: -0.3em;
        font-size: 1.625em !important;

        padding: 0.225em 0.425em;
        display: inline-block;
        -moz-border-radius: 6.25em;
        -webkit-border-radius: 6.25em;
        border-radius: 6.25em;
        border-color: @light_gray;
        background-color: @light_gray;
        &:before {
            top:0.125em;
            position: relative;
        }
    }
    .button-label-icon {
        padding-bottom: 1.3125em;
        padding-left: 2.225em;
    }
    &.white {
        background-color: @white;
        border: 0.063em solid @light_gray;
        &.on {
            background-color: @lightest_green;
            .button-icon {    
                background-color: @green;
                color: @white;
            }
        }
    }
}

.button.generative-1 {
    height: 3.334em;
    width: 18.334em;
    background-color: @lightest_gray;
    color: @darkest_gray;
    &:hover {
        background-color: @darkest_gray;
        color: @white;
        .button-icon {
            color: @white;
        }
    }
    .button-icon {
        color: @green;
        font-size: 2.001em !important;
    }
}

.button.generative-2 {
    height: 3.334em;
    width: 18.334em;
    border: 0.063em dashed @light_gray;
    border-radius: 1.5625em !important;
    background-color: @white;
    color: @gray;
    &:hover {
        color: @green;
        border: 0.063em dashed @green;
        .button-icon {
            color: @green;
        }
    }
    .button-icon {
        color: @gray;
        font-size: 1.425em !important;
    }
}

.button.generative-3 {
    color: @green;
    background-color:@white;
    &:hover {
        .button-icon {
            color: @white;

            -moz-box-shadow: 0px 0px 0.125em @green;
            -webkit-box-shadow: 0px 0px 0.125em @green;
            box-shadow: 0px 0px 0.125em @green;
            background-color: @green;
        }
    }
    .button-icon {
        color: @light_gray;

        padding: 0.063em 0.188em;
        display: inline-block;
        -moz-border-radius: 6.25em;
        -webkit-border-radius: 6.25em;
        border-radius: 6.25em;

        -moz-box-shadow: 0px 0px 0.125em @light_gray;
        -webkit-box-shadow: 0px 0px 0.125em @light_gray;
        box-shadow: 0px 0px 0.125em @light_gray;
        &.ico-comuni-aggiungi:before {
            content: "\e01f";
            position: relative;
            top: 0.125em;
        }
    }
}

.allineamento-generative {
    display: inline-block !important;
    text-align: center;
}

.green-event {
    background-color: @green !important;
    color: @white !important;
}

.gray-event {
    background-color: @lightest_gray !important;
    color: @gray !important;
    border-color: @lightest_gray !important;
}

:host-context(.contrasto-on) {
  
      .button.primary,
      .button.primary-r,      
      .button.primary-l,
      .button.default-r,
      .button.default-l,
      .button.default,
      .button.background-green,
      .button.background-gray,
      .button.background-gray-primary,
      .button.secondary,
      .button.outline,
      .button.action {
          color: @black;
          background-color: @white;
          border-color: @black;
          &:hover:not(.select) {
              color: @white;
              background-color: @black;
              border-color: @white;
          }
      }
  
      .btn.primary[disabled],
      .btn.default[disabled],
      .btn.secondary[disabled],
      .btn.outline[disabled],
      .button.action[disabled] {
          opacity: 1;
          color: @black;
          background-color: @white;
          border-color: @black;
          &:hover {
              color: @black;
              background-color: @white;
              border-color: @black;
              cursor: default;
          }
      }
  
      .button.selection-button,
      .button.generative-1,
      .button.generative-2,
      .button.generative-3 {
          color: @black;
          background-color: @white;
          border-color: @black;
          &:hover {
              color: @white;
              background-color: @black;
              border-color: @white;
          }
      }
  
      .button.action,
      .button.generative-1,
      .button.generative-2,
      .button.generative-3 {
          .button-icon {
              color: @black;
          }
          &:hover {
              &.button-icon {
                  color: @white;
              }
          }
      }
      
      .btn.action[disabled] {
          .button-icon {
              color: @black;
          }
          &:hover {
              .button-icon {
                  color: @black;
              }
          }
          &.green {
              .button-icon {
                  color: @black;
              }
          }
      }
  
      .button.select,
      .button.check,
      .button.profile,
      .button.select-profile,
      .button.select-icon-text {
          color: @black;
          background-color: @white;
          border-color: @black;
          &.on {
              color: @white !important;
              background-color: @black !important;
              border-color: @white;
          }
          &[disabled] {
              opacity: 1;
              color: @black;
              background-color: @white;
              border-color: @black;
              &:hover {
                  color: @black;
                  background-color: @white;
                  border-color: @black;
              }
              .button-icon {
                  color: @black;
              }
          }
          &.lg {
              color: @black;
              background-color: @white;
              border-color: @black;
              &.outline {
                  color: @black;
                  background-color: @white;
                  border-color: @black;
                  &:hover {
                      color: @white;
                      background-color: @black;
                      border-color: @white;
                  }
                  &.button-icon {
                      color: @white;
                  }
              }
              &.on {
                  color: @black;
                  background-color: @white;
                  border-color: @black;
                  &.button-icon {
                      color: @black;
                  }
              }
              &.button-icon {
                  color: @black;
              }
              &[disabled] {
                  opacity: 1;
                  color: @black;
                  background-color: @white;
                  border-color: @black;
                  & .button-icon{
                      color: @black;
                  }
              }
          }
      }
  
      .button.check,
      .button.select.outline,
      .button.select-icon-text {
          .button-icon {
              color: @black;
          }
          .button-icon-check {
              color: @white;
          }
      }
  
      .button.profile,
      .button.select-profile {
          .checkbox-label {
              color: @white;
          }
      }
  }